.item {
  display: flex;
  gap: 20px;
  margin-bottom: 10px;
  align-items: center;
  border: 1px solid rgb(186, 181, 181);
  border-radius: 5px;
  padding: 10px;
  background-color: #fff;
  box-shadow: 4px 1px 4px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
    1px 4px 6px rgba(0, 0, 0, 0.16);
}
.status {
  border-radius: 50%;
  width: 20px;
  height: 20px;
}
.true {
  background-color: #408900;
}
.false {
  background-color: #ff0000;
}
.avatar {
  border-radius: 2px;
}
.name {
  font-weight: 700;
  font-size: 18px;
}
